import React, { MouseEvent, useCallback, useEffect, useState } from 'react';
import { City, SortType, SortTypeColor, ESCAPE_KEY } from '../../const';
import SortOptionsList from '../sort-options-list/sort-options-list';

type SortOptionsProps = {
  activeCity: City,
  activeSortType: SortType,
  onActiveSortType: (sortType: SortType) => void
}


const SortOptions: React.FC<SortOptionsProps> = (props) => {
  const { activeCity, activeSortType, onActiveSortType } = props;

  const [isOpen, setIsOpen] = useState<boolean>(false);

  const onKeyDownEsc = useCallback((evt: KeyboardEvent) => {
    if (evt.key === ESCAPE_KEY) {
      setIsOpen(false);
    }
  }, []);

  useEffect(() => {
    setIsOpen(false);
  }, [activeCity]);

  useEffect(() => {
    if (isOpen) {
      document.addEventListener('keydown', onKeyDownEsc);
    }

    return () => {
      document.removeEventListener('keydown', onKeyDownEsc);
    };
  }, [isOpen, onKeyDownEsc]);


  return (
    <form className="places__sorting" action="#" method="get">
      <span className="places__sorting-caption">Sort by&nbsp;</span>
      <div
        onMouseEnter={(evt: MouseEvent<HTMLDivElement>) => setIsOpen(true)}
        onMouseLeave={(evt: MouseEvent<HTMLDivElement>) => setIsOpen(false)}
        className="places__sorting-type"
      >
        <span
          tabIndex={0}
          className="places__sorting-type"
          style={{ color: isOpen ? SortTypeColor.Active : SortTypeColor.Inactive }}
        >
          {activeSortType}
          <svg className="places__sorting-arrow" width="7" height="4">
            <use xlinkHref="#icon-arrow-select"></use>
          </svg>
        </span>

        <SortOptionsList
          isOpen={isOpen}
          activeSortType={activeSortType}
          onActiveSortType={onActiveSortType}
        />
      </div>
    </form>
  );
};

export default SortOptions;
